import { bro } from './bro';
import '../styles/main.scss';
import '../styles/_about.scss';
import './scroll.js';
import './swap.js';
import './rerun.js';
import './custom-gsap.js';
import './lightbox.js';
import '../styles/glide.core.scss';
import '../styles/glide.theme.scss';

import './timer.js';

import { config } from './slider.js';

document.querySelector('h4').textContent = bro(`How's it going`);

new Glide('.glide', config).mount();

function startTime() {
  const today = new Date();
  let h = today.getHours();
  let m = today.getMinutes();
  let s = today.getSeconds();
  h = checkTime(h);
  m = checkTime(m);
  s = checkTime(s);
  document.getElementById('clock').innerHTML = `${h}<br />${m}<br />${s}`;
  const t = setTimeout(startTime, 500);
}
function checkTime(i) {
  if (i < 10) {
    i = `0${i}`;
  } // add zero in front of numbers < 10
  return i;
}

document.getElementById('clock').addEventListener('load', startTime());

function date() {
  const event = new Date();

  const options = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' };
  const test23 = event.toLocaleDateString('ko-KR', options);
  // const test23 = event.toLocaleDateString('de-DE', options);

  document.getElementById('datum').innerHTML = test23;
}

document.getElementById('datum').addEventListener('load', date());

if (document.getElementById('marquee')) {
  const laufschrift = document.getElementById('marquee');
  const len = laufschrift.innerHTML.length;
  laufschrift.firstChild.style.animationDuration = `${len / 50}s`;
}

const modal = document.querySelector('.modal');
const previews = document.querySelectorAll('.gallery img');
const original = document.querySelector('.full-img');
const caption = document.querySelector('.caption');
const urlChange = document.querySelectorAll('.urlChange');

previews.forEach(preview => {
  preview.addEventListener('click', () => {
    modal.classList.add('open');
    original.classList.add('open');
    // dynamic change images
    const originalSrc = preview.src;
    preview.src = `${originalSrc}${new Date().getTime()}`;
    original.src = originalSrc;

    // original.src = `./${originalSrc}`;
    const altText = preview.alt;
    caption.textContent = altText;
  });
});

modal.addEventListener('click', e => {
  if (e.target.classList.contains('modal')) {
    modal.classList.remove('open');
    original.classList.remove('open');
    const originalSrc = previews.src;
    urlChange.src = `${originalSrc}${new Date().getTime()}`;
  }
});
