function startTimer(duration, display) {
  const start = Date.now();
  let diff;
  let minutes;
  let seconds;
  function timer() {
    // get the number of seconds that have elapsed since
    // startTimer() was called
    diff = duration - (((Date.now() - start) / 1000) | 0);

    // does the same job as parseInt truncates the float
    minutes = (diff / 60) | 0;
    seconds = diff % 60 | 0;

    minutes = minutes < 10 ? `0${minutes}` : minutes;
    seconds = seconds < 10 ? `0${seconds}` : seconds;

    display.textContent = `${minutes}:${seconds}`;

    if (diff <= 0) {
      // add one second so that the count down starts at the full duration
      // example 05:00 not 04:59

      document.querySelector('#time').innerText = 'Oh Yeah! 2 Minuten!';
      //   start = Date.now() + 1000;

      const timeIsUp = document.querySelectorAll('.test6');
      const timeIsUp1 = document.querySelectorAll('h2, h4, .marquee span');
      document.getElementById('tooltip').setAttribute('data-tooltip', '2 Minuten sind um');
      timeIsUp.forEach(preview => {
        preview.innerText = '#Oh#YEAH#2MINS#CALL#ME#CALL#ME';
      });
      timeIsUp1.forEach(preview => {
        preview.innerText = 'HI#RUF#MICH#AN';
      });
      document.title = 'HI#RUF#MICH#AN';
    }
  }
  // we don't want to wait a full second before the timer starts
  timer();
  setInterval(timer, 1000);
}

window.onload = function() {
  const fiveMinutes = 60 * 2;
  const display = document.querySelector('#time');
  startTimer(fiveMinutes, display);
};
